<template>
  <div>
    <h1></h1>
    <h2></h2>
    <form @submit.prevent="postMessage">
      <!--  -->
      <div class="input-group">
        <input v-model="title" placeholder="" maxlength="30" />
        <span class="word-count">{{ title.length }}/30</span>
      </div>
      <div class="input-group">
        <textarea v-model="content" placeholder="" maxlength="255"></textarea>
        <span class="word-count">{{ content.length }}/255</span>
      </div>
      <button type="submit"></button>
    </form>
    <div v-if="postSuccessMessage" class="success-message">{{ postSuccessMessage }}</div>
    <div v-if="postErrorMessage" class="error-message">{{ postErrorMessage }}</div>
    <h2></h2>
    <div v-if="loading">...</div>
    <div v-else-if="error">
      <p>{{ error }}</p>
    </div>
    <ul v-else>
      <li v-for="post in sortedPosts" :key="post.id">
        <h3>{{ post.title }}</h3>
        <p>{{ post.content }}</p>
        <!--  -->
        <small>: {{ formatDate(post.post_date) }}</small>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      title: '',
      content: '',
      posts: [],
      loading: true,
      error: null,
      postSuccessMessage: '',
      postErrorMessage: ''
    };
  },
  computed: {
    sortedPosts() {
      return this.posts.slice().sort((a, b) => {
        return new Date(b.post_date) - new Date(a.post_date);
      });
    }
  },
  mounted() {
    this.fetchPosts();
  },
  methods: {
    fetchPosts() {
      axios.get('/api/forum-posts')
        .then(response => {
          this.posts = response.data;
        })
        .catch(error => {
          this.error = error.message;
        })
        .finally(() => {
          this.loading = false;
        });
    },
    postMessage() {
      // 
      if (!this.title || !this.content) {
        this.postErrorMessage = '';
        this.postSuccessMessage = '';
        return;
      }
      if (this.title.length > 30) {
        this.postErrorMessage = ' 30 ';
        this.postSuccessMessage = '';
        return;
      }
      if (this.content.length > 255) {
        this.postErrorMessage = ' 255 ';
        this.postSuccessMessage = '';
        return;
      }
      axios.post('/api/forum-postsqd', {
        title: this.title,
        content: this.content
      })
      .then(response => {
        console.log(':', response.data);
        this.title = '';
        this.content = '';
        this.fetchPosts();
        // 
        this.postSuccessMessage = '';
        this.postErrorMessage = '';
        // 
        setTimeout(() => {
          this.postSuccessMessage = '';
        }, 3000);
      })
      .catch(error => {
        console.error(':', error);
        // 
        this.postErrorMessage = '';
        this.postSuccessMessage = '';
        // 
        setTimeout(() => {
          this.postErrorMessage = '';
        }, 3000);
      });
    },
    formatDate(date) {
      if (!date) return '';
      return new Intl.DateTimeFormat('zh-CN', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      }).format(new Date(date));
    }
  }
};
</script>

<style scoped>
/*  */
div {
  text-align: center;
}

h1, h2 {
  color: #2e7d32; /*  */
}

form {
  margin-bottom: 20px;
  /*  */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.input-group {
  position: relative;
  width: 50%;
}

input,
textarea {
  /*  */
  width: 100%; 
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

/*  */
textarea {
  height: 150px; 
}

.word-count {
  position: absolute;
  bottom: 15px;
  right: 10px;
  font-size: 12px;
  color: #999;
}

button {
  padding: 10px 20px;
  background-color: #4caf50; /*  */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #388e3c; /*  */
}

ul {
  list-style-type: none;
  padding: 0;
  /*  */
  display: flex;
  flex-direction: column;
  align-items: center;
}

li {
  border: 1px solid #ddd;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 4px;
  /*  100% */
  width: 90%; 
  position: relative; /*  */
}

h3 {
  text-align: center; /*  */
  color: #2e7d32; /*  */
}

p {
  text-indent: 2em; /*  */
  text-align: left; /*  */
  /*  */
  word-wrap: break-word;
  word-break: break-all;
}

small {
  position: absolute; /*  */
  bottom: 10px; /*  10px */
  right: 10px; /*  10px */
  color: #2e7d32; /*  */
}

/*  */
.success-message {
  color: #2e7d32; /*  */
  background-color: #e8f5e9; /*  */
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
}

.error-message {
  color: #d32f2f;
  background-color: #ffebee;
  padding: 10px;
  border-radius: 4px;
  margin-bottom: 10px;
}
</style>