<template>
  <div class="admin-container">
    <div class="admin-header">
      <h1></h1>
      <button class="logout-btn" @click="logout"></button>
    </div>
    <!--  -->
    <div class="admin-tabs">
      <button 
        v-for="tab in tabs" 
        :key="tab.id"
        @click="currentTab = tab.id"
        :class="{ active: currentTab === tab.id }"
      >
        {{ tab.name }}
      </button>
    </div>

    <!--  -->
    <div v-if="currentTab === 'users'" class="admin-section">
      <h2></h2>
      <div class="search-bar">
        <input v-model="userSearch" placeholder="" />
        <button @click="searchUsers"></button>
        <button @click="showAddUser = true"></button>
      </div>
      
      <!--  -->
      <div v-if="showAddUser" class="form-container">
        <input v-model="newUser.username" placeholder="" />
        <input v-model="newUser.password" type="password" placeholder="" />
        <!--  -->
        <label>
          <input type="checkbox" v-model="newUser.isAdmin" :true-value="1" :false-value="0" /> 
        </label>
        <button @click="addUser"></button>
        <button @click="showAddUser = false"></button>
      </div>

      <!--  -->
      <table class="admin-table">
        <thead>
          <tr>
            <th>ID4</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" :key="user.id">
            <td>{{ user.id }}</td>
            <td>
              <span v-if="!user.editing">{{ user.username }}</span>
              <input v-else v-model="user.editData.username" />
            </td>
            <td>
              <span v-if="!user.editing">{{ user.password }}</span>
              <input v-else v-model="user.editData.password" type="password" />
            </td>
            <!--  -->
            <td>
              <span v-if="!user.editing">{{ user.isAdmin === 1 ? '' : '' }}</span>
              <label v-else>
                <input type="checkbox" v-model="user.editData.isAdmin" :true-value="1" :false-value="0" />
                {{ user.editData.isAdmin === 1 ? '' : '' }}
              </label>
            </td>
            <td>
              <button v-if="!user.editing" @click="startEdit(user)"></button>
              <template v-else>
                <button @click="saveEdit(user)"></button>
                <button @click="cancelEdit(user)"></button>
              </template>
              <button @click="deleteUser(user.id)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!--  -->
    <div v-if="currentTab === 'products'" class="admin-section">
      <h2></h2>
      <div class="search-bar">
        <input v-model="productSearch" placeholder="" />
        <button @click="searchProducts"></button>
        <button @click="showAddProduct = true"></button>
      </div>
      
      <!--  -->
      <div v-if="showAddProduct" class="form-container">
        <input v-model="newProduct.name" placeholder="" />
        <input v-model="newProduct.description" placeholder="" />
        <button @click="addProduct"></button>
        <button @click="showAddProduct = false"></button>
      </div>
    
      <!--  -->
      <table class="admin-table">
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="product in products" :key="product.id">
            <td>{{ product.id }}</td>
            <td>
              <span v-if="!product.editing">{{ product.name }}</span>
              <input v-else v-model="product.editData.name" />
            </td>
            <td>
              <span v-if="!product.editing">{{ product.description }}</span>
              <input v-else v-model="product.editData.description" />
            </td>
            <td>
              <button v-if="!product.editing" @click="startEditProduct(product)"></button>
              <template v-else>
                <button @click="saveEditProduct(product)"></button>
                <button @click="cancelEditProduct(product)"></button>
              </template>
              <button @click="deleteProduct(product.id)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!--  -->
    <div v-if="currentTab === 'garbage'" class="admin-section">
      <h2></h2>
      <div class="search-bar">
        <input v-model="garbageSearch" placeholder="" />
        <button @click="searchGarbageItems"></button>
        <button @click="showAddGarbage = true"></button>
      </div>
      
      <!--  -->
      <div v-if="showAddGarbage" class="form-container">
        <input v-model="newGarbageItem.name" placeholder="" />
        <select v-model="newGarbageItem.category">
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
        <input v-model="newGarbageItem.description" placeholder="" />
        <button @click="addGarbageItem"></button>
        <button @click="showAddGarbage = false"></button>
      </div>
    
      <!--  -->
      <table class="admin-table">
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in garbageItems" :key="item.id">
            <td>{{ item.id }}</td>
            <td>
              <span v-if="!item.editing">{{ item.name }}</span>
              <input v-else v-model="item.editData.name" />
            </td>
            <td>
              <span v-if="!item.editing">{{ item.category }}</span>
              <select v-else v-model="item.editData.category">
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
                <option value=""></option>
              </select>
            </td>
            <td>
              <span v-if="!item.editing">{{ item.description }}</span>
              <input v-else v-model="item.editData.description" />
            </td>
            <td>
              <button v-if="!item.editing" @click="startEditGarbageItem(item)"></button>
              <template v-else>
                <button @click="saveEditGarbageItem(item)"></button>
                <button @click="cancelEditGarbageItem(item)"></button>
              </template>
              <button @click="deleteGarbageItem(item.id)"></button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>

    <!--  -->
    <!-- template -->
    <div v-if="currentTab === 'events'" class="admin-section">
    <h2></h2>
    
    <div class="search-bar">
        <input v-model="eventSearch" placeholder="..." />
        <button @click="searchEvents"></button>
        <button @click="showAddEvent = true"></button>
    </div>
    
    <div v-if="showAddEvent" class="add-form">
        <h3></h3>
        <input v-model="newEvent.event_name" placeholder="" />
        <input v-model="newEvent.location" placeholder="" />
        <textarea v-model="newEvent.description" placeholder=""></textarea>
        <input v-model="newEvent.start_date" type="datetime-local" placeholder="" />
        <input v-model="newEvent.end_date" type="datetime-local" placeholder="" />
        <button @click="addEvent"></button>
        <button @click="showAddEvent = false"></button>
    </div>
    
 
    
    <!--  -->
    <table class="admin-table">
        <thead>
            <tr>
                <th>ID</th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
                <th></th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="event in filteredEvents" :key="event.id">
                <td>{{ event.id }}</td>
                <td>
                <span v-if="!event.editing">{{ event.event_name }}</span>
                <input v-else v-model="event.tempData.event_name" />
            </td>
            <td>
                <span v-if="!event.editing">{{ event.location }}</span>
                <input v-else v-model="event.tempData.location" />
            </td>
            <td>
                <span v-if="!event.editing">{{ event.description }}</span>
                <textarea v-else v-model="event.tempData.description"></textarea>
            </td>
            <td>
                <span v-if="!event.editing">{{ formatDate(event.start_date) }}</span>
                <input v-else v-model="event.tempData.start_date" type="datetime-local" />
            </td>
            <td>
                <span v-if="!event.editing">{{ formatDate(event.end_date) }}</span>
                <input v-else v-model="event.tempData.end_date" type="datetime-local" />
            </td>
            <td>
                <template v-if="!event.editing">
                    <button @click="editEvent(event)"></button>
                    <button @click="deleteEvent(event.id)"></button>
                </template>
                <template v-else>
                    <button @click="saveEditEvent(event)"></button>
                    <button @click="cancelEditEvent(event)"></button>
                </template>
            </td>
            </tr>
        </tbody>
    </table>
</div>

    <!--  -->
    <!--  -->
<div v-if="currentTab === 'items'" class="admin-section">
    <h2></h2>
    
    <div class="search-bar">
        <input v-model="itemSearch" placeholder="" />
        <button @click="searchItems"></button>
        <button @click="showAddItem = true"></button>
    </div>
    
    <!--  -->
    <!--  -->
<div v-if="showAddItem" class="form-container">
    <input v-model="newItem.item_name" placeholder="" />
    <textarea v-model="newItem.item_description" placeholder=""></textarea>
    <input v-model="newItem.phone_number" placeholder="" /> <!--  -->
    <button @click="addItem"></button>
    <button @click="showAddItem = false"></button>
</div>
    <!--  -->
    <table class="admin-table">
    <thead>
        <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th> <!--  -->
            <th></th>
            <th></th>
        </tr>
    </thead>
    <tbody>
        <tr v-for="item in filteredItems" :key="item.id">
            <td>{{ item.id }}</td>
            <td>
                <span v-if="!item.editing">{{ item.item_name }}</span>
                <input v-else v-model="item.editData.item_name" />
            </td>
            <td>
                <span v-if="!item.editing">{{ item.item_description }}</span>
                <textarea v-else v-model="item.editData.item_description"></textarea>
            </td>
            <td>
                <span v-if="!item.editing">{{ item.phone_number }}</span>
                <input v-else v-model="item.editData.phone_number" /> <!--  -->
            </td>
            <td>{{ formatDate(item.publish_date) }}</td>
            <td>
        <template v-if="!item.editing">
            <button @click="startEditItem(item)"></button>
            <button @click="deleteItem(item.id)"></button>
        </template>
        <template v-else>
            <button @click="saveEditItem(item)"></button>
            <button @click="cancelEditItem(item)"></button>
        </template>
    </td>
        </tr>
    </tbody>
</table>
</div>

    <!--  -->
    <div v-if="currentTab === 'orders'" class="admin-section">
  <h2></h2>
  <div class="search-bar">
    <input v-model="orderSearch" placeholder="" />
    <button @click="searchOrders"></button>
  </div>
  
  <table class="admin-table">
    <thead>
      <tr>
        <th>ID</th>
        <th></th>
        <th></th>
        <th></th>  <!--  -->
        <th></th>
        <th></th>
        <th></th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="order in filteredOrders" :key="order.id">
        <td>{{ order.id }}</td>
        <td>{{ order.product_name }}</td>
        <td>{{ order.quantity }}</td>
        <td>{{ order.phone_number }}</td>  <!--  -->
        <td>{{ order.address }}</td>
        <td>{{ formatDate(order.order_date) }}</td>
        <td>{{ order.status }}</td>
        <td>
          <button v-if="order.status === ''" @click="approveOrder(order.id)"></button>
          <button v-if="order.status === ''" @click="rejectOrder(order.id)"></button>
        </td>
      </tr>
    </tbody>
  </table>
  
  <!--  -->
  <div v-if="showRejectDialog" class="dialog-overlay">
    <div class="dialog">
      <h3></h3>
      <textarea v-model="rejectReason"></textarea>
      <button @click="confirmReject"></button>
      <button @click="showRejectDialog = false"></button>
    </div>
  </div>
</div>
<!--  -->
<div v-if="currentTab === 'carbon'" class="admin-section">
  <h2></h2>
  
  <button @click="showAddTravelType = true"></button>
  
  <div v-if="showAddTravelType" class="form-container">
    <input v-model="newTravelType.travel_type" placeholder="" />
    <input v-model.number="newTravelType.carbon_reduction_per_unit" type="number" step="0.01" placeholder="(kg)" />
    <button @click="addTravelType"></button>
    <button @click="showAddTravelType = false"></button>
  </div>

  <table class="admin-table">
    <thead>
      <tr>
        <th>ID</th>
        <th></th>
        <th>(kg)</th>
        <th></th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="type in travelTypes" :key="type.id">
        <td>{{ type.id }}</td>
  <td>
    <span v-if="!type.editing">{{ type.travel_type }}</span>
    <input v-else v-model="type.editData.travel_type" />
  </td>
  <td>
    <span v-if="!type.editing">{{ type.carbon_reduction_per_unit }}</span>
    <input v-else 
           v-model.number="type.editData.carbon_reduction_per_unit" 
           type="number" 
           step="0.01" />
  </td>
        <td>
  <template v-if="!type.editing">
    <button @click="editTravelType(type)"></button>
    <button @click="deleteTravelType(type.id)"></button>
  </template>
  <template v-else>
    <button @click="saveTravelType(type)"></button>
    <button @click="cancelEditTravelType(type)"></button>
  </template>
</td>
      </tr>
    </tbody>
  </table>
</div>

    <!--  -->
    <!--  -->
<div v-if="currentTab === 'forum'" class="admin-section">
  <h2></h2>
  
  <div class="search-bar">
    <input v-model="forumSearch" placeholder="" />
    <button @click="searchForumPosts"></button>
    <button @click="showAddForumPost = true"></button>
  </div>
  
  <!--  -->
  <div v-if="showAddForumPost" class="form-container">
    <input v-model="newForumPost.title" placeholder="" />
    <textarea v-model="newForumPost.content" placeholder=""></textarea>
    <button @click="addForumPost"></button>
    <button @click="showAddForumPost = false"></button>
  </div>
  
  <!--  -->
  <div v-if="editingForumPost" class="form-container">
    <input v-model="editingForumPost.title" placeholder="" />
    <textarea v-model="editingForumPost.content" placeholder=""></textarea>
    <button @click="saveForumPost"></button>
    <button @click="editingForumPost = null"></button>
  </div>
  
  <!--  -->
  <!--  -->
<table class="admin-table forum-management-table">
  <thead>
    <tr>
      <th>ID</th>
      <th></th>
      <th></th>
      <th></th>
      <th></th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="post in forumPosts" :key="post.id">
      <td>{{ post.id }}</td>
      <td>
        <template v-if="!post.editing">{{ post.title }}</template>
        <input v-else v-model="post.editData.title" />
      </td>
      <td>
        <template v-if="!post.editing">{{ post.content }}</template>
        <textarea v-else v-model="post.editData.content"></textarea>
      </td>
      <td>{{ formatDate(post.post_date) }}</td>
      <td>
        <template v-if="!post.editing">
          <button @click="startEditForumPost(post)"></button>
          <button @click="deleteForumPost(post.id)"></button>
        </template>
        <template v-else>
          <button @click="saveEditForumPost(post)"></button>
          <button @click="cancelEditForumPost(post)"></button>
        </template>
      </td>
    </tr>
  </tbody>
</table>
</div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  name: 'Admin',
  data() {
    return {
      currentTab: 'users',
      tabs: [
        { id: 'users', name: '' },
        { id: 'products', name: '' },
        { id: 'garbage', name: '' },
        { id: 'events', name: '' },
        { id: 'items', name: '' },
        { id: 'orders', name: '' },
        { id: 'carbon', name: '' },
        { id: 'forum', name: '' }
      ],
      users: [], // 
      userSearch: '', // 
      showAddUser: false, // 
      newUser: {
        username: '',
        password: '',
        isAdmin: 0 // 0
      },
      // 
      
        originalProducts: [], // 
        productSearch: '',
      products: [], // 
      productSearch: '', // 
      showAddProduct: false, // 
      newProduct: { // 
        name: '',
        description: ''
      },
      // 
        originalGarbageItems: [], // 
        garbageSearch: '', // 
      garbageItems: [], // 
    garbageSearch: '', // 
    showAddGarbage: false, // 
    newGarbageItem: { // 
      name: '',
      category: '',
      description: ''
    },
    events: [], // 
        eventSearch: '', // 
        showAddEvent: false, // 
        newEvent: { // 
            event_name: '',
            start_date: '',
            end_date: '',
            
        },
        events: [], // 
        filteredEvents: [], // computeddata
        
        newEvent: { // 
            event_name: '',
            start_date: '',
            end_date: '',
            location: '', // 
            description: '' // 
        },
        items: [], // 
        filteredItems: [], // filteredItems
        itemSearch: '', // 
        showAddItem: false, // 
        newItem: { // 
            item_name: '',
            item_description: '',
            phone_number: ''
        },
        // 
        orders: [], // 
        orderSearch: '', // 
        filteredOrders: [], // filteredOrders
        showAddOrder: false, // 
        selectedOrderId: null,
        rejectReason: '', // 
        showRejectDialog: false, // 
    selectedOrderId: null,
    rejectReason: '',
        newOrder: { // 
            user_id: '', // ID
            order_date: '', // 
            total_amount: 0 // 
        },
        // 
    travelTypes: [],
    newTravelType: {
      travel_type: '',
      carbon_reduction_per_unit: 0
    },
    showAddTravelType: false, // 
  

        // 

        forumPosts: [], // 
    forumSearch: '', // 
    showAddForumPost: false, // 
    newForumPost: { // 
      title: '',
      content: ''
    },
    editingForumPost: null // 
    
    };
  },
  computed: {
    // filteredEvents
    // filteredEvents() {
    //     return this.filteredEvents; // 
    // }
},
  mounted() {
    this.fetchUsers();
    this.fetchProducts(); // 
    this.fetchGarbageItems(); // 
    this.fetchEvents(); // 
    this.fetchForumPosts(); // 
  },
  methods: {
    //
    logout() {
      localStorage.removeItem('token');
      localStorage.removeItem('userRole');
      this.$router.push('/login');
    },
    // 
    fetchUsers() {
      axios.get('/api/admin/users')
        .then(response => {
          this.users = response.data.map(user => ({
            ...user,
            editing: false,
            editData: null
          }));
        });
    },
    searchUsers() {
      axios.get(`/api/admin/users?search=${this.userSearch}`)
        .then(response => {
          this.users = response.data;
        });
    },
    addUser() {
    // 
    const usernameExists = this.users.some(user => 
      user.username.toLowerCase() === this.newUser.username.toLowerCase()
    );
    
    if (usernameExists) {
      alert('');
      return;
    }

    axios.post('/api/admin/users', this.newUser)
      .then(() => {
        this.fetchUsers();
        this.showAddUser = false;
        this.newUser = { username: '', password: '', isAdmin: false };
      })
      .catch(error => {
        console.error(':', error);
        alert('');
      });
  },
    editUser(user) {
      // 
    },
    deleteUser(id) {
      if (confirm('')) {
        axios.delete(`/api/admin/users/${id}`)
          .then(() => {
            this.fetchUsers();
          });
      }
    },
    
    // 
    startEdit(user) {
      this.$set(user, 'editing', true);
      this.$set(user, 'editData', {
        username: user.username,
        password: user.password,
        isAdmin: user.isAdmin
      });
    },
    
    // 
    saveEdit(user) {
      axios.put(`/api/admin/users/${user.id}`, user.editData)
        .then(() => {
          Object.assign(user, user.editData);
          user.editing = false;
          this.fetchUsers(); // 
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    
    // 
    cancelEdit(user) {
      user.editing = false;
    },
    
    // 
    fetchUsers() {
      axios.get('/api/admin/users')
        .then(response => {
          this.users = response.data.map(user => ({
            ...user,
            editing: false,
            editData: null
          }));
        });
    },
    // 
    fetchGarbageItems() {
    axios.get('/api/garbage-items')
        .then(response => {
            this.originalGarbageItems = response.data.map(item => ({
                ...item,
                editing: false,
                editData: null
            }));
            this.garbageItems = [...this.originalGarbageItems]; // 
        });
},
  searchGarbageItems() {
        if (!this.garbageSearch) {
            this.garbageItems = [...this.originalGarbageItems];
            return;
        }
        this.garbageItems = this.originalGarbageItems.filter(item => 
            item.name && item.name.toLowerCase().includes(this.garbageSearch.toLowerCase())
        );
    },
  addGarbageItem() {
    axios.post('/api/garbage-items', this.newGarbageItem)
      .then(() => {
        this.fetchGarbageItems();
        this.showAddGarbage = false;
        this.newGarbageItem = { name: '', category: '', description: '' };
      });
  },
  startEditGarbageItem(item) {
    this.$set(item, 'editing', true);
    this.$set(item, 'editData', {
      name: item.name,
      category: item.category,
      description: item.description
    });
  },
  saveEditGarbageItem(item) {
    axios.put(`/api/garbage-items/${item.id}`, item.editData)
      .then(() => {
        Object.assign(item, item.editData);
        item.editing = false;
        this.fetchGarbageItems();
      });
  },
  cancelEditGarbageItem(item) {
    item.editing = false;
  },
  deleteGarbageItem(id) {
    if (confirm('')) {
      axios.delete(`/api/garbage-items/${id}`)
        .then(() => {
          this.fetchGarbageItems();
        });
    }
  },
    // 
    fetchProducts() {
    axios.get('/api/admin/products')
        .then(response => {
            this.originalProducts = response.data.map(product => ({
                ...product,
                editing: false,
                editData: null
            }));
            this.products = [...this.originalProducts]; // 
        });
},
    searchProducts() {
        if (!this.productSearch) {
            this.products = [...this.originalProducts];
            return;
        }
        this.products = this.originalProducts.filter(product => 
            product.name && product.name.toLowerCase().includes(this.productSearch.toLowerCase())
        );
    },
    addProduct() {
      axios.post('/api/admin/products', this.newProduct)
        .then(() => {
          this.fetchProducts();
          this.showAddProduct = false;
