  axios.put(`/api/admin/reservation-orders/${this.selectedOrderId}/reject`, 
    { reason: this.rejectReason }, // JSON
    {
      headers: {
        'Content-Type': 'application/json' // Content-Type
      }
    }
  )
    .then(() => {
