<template>
  <div class="carbon-calculator">
    <h1></h1>
    <form @submit.prevent="calculateCarbonFootprint">
      <div class="form-group">
        <label></label>
        <select v-model="selectedTravelType">
          <option v-for="travelType in travelTypes" :key="travelType.id" :value="travelType">
            {{ travelType.travel_type }}
          </option>
        </select>
      </div>
      <div class="form-group">
        <label>(km)</label>
        <input v-model.number="distance" type="number" min="0" placeholder="" />
      </div>
      <button type="submit"></button>
    </form>
    <div v-if="carbonFootprint" class="result-container">
      <!-- <p>: {{ carbonFootprint }} </p> -->
      <p>  {{ carbonFootprint }} </p>
      <p v-if="carbonFootprint < 1" class="suggestion"> ~</p>
      <p v-else-if="carbonFootprint < 5" class="suggestion"> </p>
      <p v-else-if="carbonFootprint < 10" class="suggestion"> 10</p>
      <p v-else class="suggestion"> ~</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      travelTypes: [],
      selectedTravelType: null,
      distance: 0,
      carbonFootprint: null
    };
  },
  mounted() {
    this.fetchTravelTypes();
  },
  methods: {
    fetchTravelTypes() {
      axios.get('/api/carbon-calculator/travel-types')
        .then(response => {
          this.travelTypes = response.data;
        })
        .catch(error => {
          console.error(':', error);
        });
    },
    calculateCarbonFootprint() {
      if (this.selectedTravelType && this.distance > 0) {
        this.carbonFootprint = this.selectedTravelType.carbon_reduction_per_unit * this.distance;
      } else {
        this.carbonFootprint = null;
      }
    }
  }
};
</script>

<style scoped>
.carbon-calculator {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

.carbon-calculator h1 {
  text-align: center;
  color: #2e7d32; /*  */
  margin-bottom: 30px;
}

form {
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
  width: 100%;
  max-width: 400px;
}

.form-group label {
  width: 80px;
  margin-right: 10px;
  text-align: right;
  color: #2e7d32;
}

.form-group select,
.form-group input {
  flex: 1;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

button {
  padding: 10px 20px;
  background-color: #4caf50; /*  */
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

button:hover {
  background-color: #388e3c; /*  */
}

div p {
  margin: 10px 0;
  color: #2e7d32; /*  */
}

.result-container {
  text-align: center;
}

.suggestion {
  margin: 10px auto;
  max-width: 80%;
}

@media (max-width: 768px) {
  .form-container {
    padding: 15px;
  }
  
  .form-group {
    flex-direction: column;
  }
  
  .form-group label {
    text-align: left;
    margin-bottom: 5px;
  }
}
</style>